@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;700&display=swap');

body {
  font-family: 'Oswald', sans-serif;
  color: #d9e1ec;
}

.garage-panel {
  position: fixed;
  top: 70px;
  right: 30px;
  width: 360px;
  height: 800px;
  background-color: #101010;
  padding: 16px;
  border-radius: 5px;
  padding: 16px;
  box-shadow: 0 0 20px #000a;
  overflow-y: auto;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
}

.tab-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 14px;
}

.bubble-btn {
  flex: 1;
  margin: 0 4px;
  background: #263d6a;
  border: 2px solid #3b4e75;
  color: #cfd8e6;
  padding: 10px 12px;
  border-radius: 5px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition:  0.2s;
}

.bubble-btn i {
  margin-right: 6px;
}

.vehicle-entry,
.history-entry {
  background: #314261;
  border: 1px solid #1e2f4a;
  border-radius: 5px;
  padding: 12px;
  margin-bottom: 10px;
}

.vehicle-info p {
  margin: 4px 0;
  font-size: 16px;
}

.vehicle-info span {
  font-weight: 600;
  color: #a4cbff;
}

.spawn-btn {
  background: #355492;
  color: white;
  border: none;
  padding: 8px 14px;
  border-radius: 5px;
  margin-top: 10px;
  width: 100%;
  cursor: cell;
}

.spawn-btn i {
  margin-right: 6px;
}
